<template>
<div class="signupinfo">
    <div class="process">
        <div class="process-signup">
            <div class="number current">1</div>
            <span>報名資訊</span>
        </div>

        <div class="process-signup">
            <div class="number current">2</div>
            <span>付款資訊</span>
        </div>

        <div class="process-signup">
            <div class="number unfinis">3</div>
            <span>送出報名</span>
        </div>
    </div>
    <div class="paymentinfo">
        <h3 v-show="eventinfo[12]=='否'">此活動為免費活動</h3>
        <div class="paymentmethods" v-show="eventinfo[12]=='是'">
            <div class="price">{{eventinfo[13]}}/人</div>
            付款方式：
            <h5 v-for="(item,index) of paymentmenthods" :key="index">

                {{item}}
                <h5 v-show="item=='匯款'">帳號：{{eventinfo[15]}}</h5>
            </h5>

        </div>
    </div>
    <div class="btnwrap">
        <fillbtn btnname="上一步" width="147" height="43" @clickhandler="signupinfo"></fillbtn>
        <fillbtn btnname="下一步" width="147" height="43" @clickhandler="tochecksignupinfo"></fillbtn>
    </div>
</div>
</template>

<script>
import fillbtn from '../components/fillbtn'
export default {
    data() {
        return {

        }
    },
    components: {
        fillbtn
    },
    methods: {
        signupinfo() {
            this.$store.commit('minussignupnumber');
        },
        tochecksignupinfo() {
            this.$store.commit('addsignupnumber');
        }
    },
    computed: {
        eventinfo() {
            return this.$store.state.signupinfo
        },
        paymentmenthods() {
            if (this.eventinfo[12] == '是') {
                return this.$store.state.signupinfo[14].split('、')
            } else {
                return null
            }

        }
    }
}
</script>

