<template>
<div class="ticket">
    <div class="ticketinfo">
        <div class="date">
            <span>{{signupinfolist[2]}}</span>
        </div>
        <div class="time">
            <span>{{signupinfolist[3]}}</span>
        </div>
    </div>

    <div class="buybtn">
        <i class="ri-add-line" @click="add"></i>
        <div></div>
        <i class="ri-subtract-line" @click="minus"></i>
    </div>
    <div class="ticketnumber" v-show="numbershow">
        <span>{{ticketnumber}}</span>
    </div>
</div>
</template>

<script>

export default {
    data() {
        return {
            ticketnumber: 1,
            numbershow: true
        }
    },
    methods: {
        add() {

            // if (this.ticketnumber == 0) {
            //     this.numbershow = false
            //     this.ticketnumber == 0
            // } else {
            //     this.numbershow = true
            // }
            this.ticketnumber++;
            if (this.ticketnumber > 1) {
                this.ticketnumber = 1;
            }
            this.numbershow = true

            this.$store.commit('setticketnumber', this.ticketnumber)

        },
        minus() {
            this.ticketnumber--;
            if (this.ticketnumber <= 0) {
                this.numbershow = false
                this.ticketnumber = 0
                this.$store.commit('setticketnumber', this.ticketnumber)
            }

        },
        convertdate(date) {
            let str = date
            // console.log(str)
            let strlist = str.split('-')
            let result = (strlist[0] + '/' + strlist[1] + '/' + strlist[2]).substring(0, 10)

            return result
        },
    },
    computed: {
        signupinfolist() {
            return this.$store.state.signupinfo
        }
    }
}
</script>

