import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import anime from "animejs/lib/anime.es.js";
import liff from "@line/liff";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    memberinfo: [],
    eventid: "",
    ticketnumber: 1,
    signupprogressnum: 0,
    singuplist: [],
    signupinfo: [],
    postsignupinfo: [],
    showsidemenu: false,
    userprofiledata: {},
    usereventinfo: [],
    searchkeyword: "",
    test: {},
    hostid: "",
    profilecurrentpage: "myact",
    alertshow: false,
    recentactinfo: [],
  },
  mutations: {
    sethostid(state, payload) {
      state.hostid = payload;
    },
    setpostsignupinfolength(state, payload) {
      state.postsignupinfo.length = payload;
    },
    setpostsignupinfo(state, payload) {
      state.postsignupinfo = payload;
    },
    seteventid(state, payload) {
      state.eventid = payload;
    },
    addsignupnumber(state) {
      state.signupprogressnum++;
    },
    minussignupnumber(state) {
      state.signupprogressnum--;
    },
    setsignuplist(state, payload) {
      state.singuplist = payload.split("、");
    },
    setticketnumber(state, payload) {
      state.ticketnumber = payload;
    },
    setsignupinfo(state, payload) {
      state.signupinfo = payload;
    },
    setshowsidemenu(state, payload) {
      state.showsidemenu = payload;
    },
    setuserdata(state, payload) {
      state.userprofiledata = payload;
    },
    setuseremail(state, payload) {
      state.userprofiledata.mail = payload;
    },
    setusereventinfo(state, payload) {
      state.usereventinfo = payload;
    },
    setsearchkeyword(state, payload) {
      state.searchkeyword = payload;
    },
    resetsignupinfo(state, payload) {
      state.signupprogressnum = payload;
      state.ticketnumber = payload;
      state.postsignupinfo = [];
    },
    settest(state, payload) {
      state.test = payload;
    },
    setprofilecurrentpage(state, payload) {
      state.profilecurrentpage = payload;
    },
    setalertshow(state, payload) {
      state.alertshow = payload;
    },
    setrecentactinfo(state, payload) {
      state.recentactinfo = payload;
    },
  },
  actions: {
    // getSignupinfo() {
    //   axios({
    //     method: "get",
    //     url:
    //       "https://script.google.com/macros/s/AKfycbwu0IumvrBT9W5XybuCu-_hQxn8r8i37EQ-ziiR0N3Mfjkilx2ww_NRS8kEtqNMKV_I/exec",
    //     params: {
    //       mode: "query",
    //       id: "950ac145-5aa9-4456-b48a-03e0b2da05ff",
    //     },
    //   }).then((response) => {
    //     this.eventinfo = response.data[0];

    //     this.regisinfo = response.data[0][9].split("、");
    //     this.$store.commit("setmemberinfolength", this.regisinfo.length);
    //     console.log(response.data[0]);
    //   });
    // },
    getRecenteventinfo() {
     
    },
    sendmsg() {
     
      // window.alert(context.state.recentactinfo[0][0])
      
    },
    async initliff({ commit, dispatch }) {
      try {
        await liff.init({
          liffId: "1655851081-DP32Go8k", // 請用自己的 liffId
        });
        liff
          .getProfile()
          .then((profile) => {
            commit("setuserdata", profile);
            dispatch("getUserEventinfo");

            // const name = profile.displayName;
          })
          .catch((err) => {
            console.log("init error   ", err);
          });
        // dispatch('sendmsg')
        // var user = liff.getDecodedIDToken();
        // commit("setuseremail", user);

        // alert('init succes')
      } catch (err) {
        console.log(err.code, err.message);
        alert(err.message);
      }
    },
    getUserEventinfo(context) {
      axios
        .get(
          "https://script.google.com/macros/s/AKfycbxqMsajmkgXY6oxaJlCh3tRP5Rr8MEh0GpBSkcR7amFy7cL_YorIGZvftoQnSv6Nn7R8Q/exec",
          {
            params: {
              mode: "profileevent",
              id: "",
              userid: context.state.userprofiledata.userId,
            },
          }
        )
        .then((res) => {
          console.log('有抓到使用者資料',res.data)
          // alert("有抓到使用者資料",res.data[0])
          context.commit("setusereventinfo", res.data);
        })
        .catch((error) => {
          console.error(error);
        });
    },
    sidemenuani({ commit }, showmode) {
      console.log(commit, showmode);
      if (showmode == "show") {
        anime({
          targets: ".menu",
          opacity: 1,
          translateX: [200, 0],
          easing: "easeInOutQuad",
          duration: 450,
        });
        commit("setshowsidemenu", true);
      }
      if (showmode == "close") {
        anime({
          targets: ".menu",
          translateX: 200,
          opacity: 0,
          easing: "easeInOutQuad",
          duration: 450,
        });
        setTimeout(() => {
          commit("setshowsidemenu", false);
        }, 460);
      }
    },
    showalertani({ commit }, showmode) {
      if (showmode == "show") {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        anime({
          targets: ".alertblock",
          top: "120px",
          easing: "easeInOutQuad",
          duration: 450,
        });
        commit("setalertshow", true);
      }
      if (showmode == "close") {
        anime({
          targets: ".alertblock",
          top: "0px",
          easing: "easeInOutQuad",
          duration: 450,
        });
        commit("setalertshow", false);
      }
    },
    postmemberinfo(context) {
      let postsignupinfo = "";
      for (let i = 0; i < context.state.postsignupinfo.length; i++) {
        if (
          context.state.postsignupinfo[i] == "" ||
          context.state.postsignupinfo[i] === undefined
        ) {
          console.log("有歐");
          postsignupinfo = postsignupinfo + "空" + "、";
        } else {
          postsignupinfo =
            postsignupinfo + context.state.postsignupinfo[i] + "、";
        }
      }
      postsignupinfo = postsignupinfo.substring(0, postsignupinfo.length - 1);

      let postdata = {
        data: postsignupinfo,
        id: context.state.eventid,
        userid: context.state.userprofiledata.userId,
      };
      // console.log(postdata);

      axios
        .get(
          "https://script.google.com/macros/s/AKfycby2MMzEIrsHOCdh0t8yC_K6LiNSOB-fMe4uYIrCLTyp0vxv_sunXbAHFR3KPaPFPZaw/exec",
          {
            params: {
              memberinfo: postdata.data,
              id: postdata.id,
              userid: context.state.userprofiledata.userId,
            },
          }
        )
        .then((res) => {
          context.commit("addsignupnumber");
          console.table(res.data);
        })
        .catch((error) => {
          console.error(error);
        });
    },
  },
  modules: {},
});
