<template>
<div class="actarea">
    <div class="actarea-title">
        <div class="decoline"></div>
        <h4>{{title}}</h4>
    </div>
    <h5 class="actarea-describe">{{describe}}</h5>

    <vuescroll :ops="ops">
        <div class="wrap">
            <ActBlock :pic="item[1]" :eventid="item[17]" :name="item[0]" :loc="item[4]" :date="convertdate(item[2])" :time="item[3]" :number="item[7]" :waitingnumber="item[8]" v-for="(item,index) of eventlist" :key="index" :fee="item[13]" :type="item[6]"></ActBlock>
        </div>
    </vuescroll>

    <!-- <ActBlock></ActBlock>
        <ActBlock></ActBlock> -->

</div>
</template>

<script>
import ActBlock from '../components/Actblock'
import vuescroll from 'vuescroll';
import axios from 'axios'
export default {
    props: ['title', 'describe'],
    data() {
        return {
            eventlist: [],
            ops: {
                vuescroll: {
                    mode: 'native',
                    sizeStrategy: 'percent',
                    detectResize: true,
                    /** Enable locking to the main axis if user moves only slightly on one of them at start */
                    locking: true,
                },
                scrollPanel: {
                    // initialScrollY: false,
                    // initialScrollX: false,
                    scrollingX: true,
                    scrollingY: false,
                    speed: 300,
                    // easing: undefined,
                    verticalNativeBarPos: 'right'
                },
                bar: {
                    showDelay: 500,
                    onlyShowBarOnScroll: true,
                    keepShow: false,
                    background: '#c1c1c1',
                    opacity: 1,
                    hoverStyle: false,
                    specifyBorderRadius: false,
                    minSize: 0,
                    size: '6px',
                    disable: false
                }
            }
        }
    },
    created() {
        axios({
                method: 'get',
                url: 'https://script.google.com/macros/s/AKfycbzE-4_VcXcDJKk62FGK_2GaoncOW9DANFo-bdbUSIg9GDoO-hjy3hJTvGjF8t7d-8f-/exec',
                params: {
                    mode: 'hostid',
                    id: this.$route.params.hostid
                }
            })
            .then((response) => {
                console.log(response.data)
                this.$emit('dataisloaded')
                this.eventlist = response.data;
            })
    },
    mounted() {

    },
    methods: {
        convertdate(date) {
            let str = date
            // console.log(str)
            let strlist = str.split('-')
            let result = (strlist[0] + '/' + strlist[1] + '/' + strlist[2]).substring(0, 10)

            return result
        },
    },
    components: {
        ActBlock,
        vuescroll
    }
}
</script>

