<template>
<div class="catatag unclick" @click="selecttype">
    <span>{{tagname}}</span>
</div>
</template>

<script>
export default {
    props: ['tagname', 'tagindex'],
    data() {
        return {
            typename: this.tagname,
            currenttagindex: this.tagindex
        }
    },
    methods: {
        selecttype() {
            this.$emit('typehandler', this.typename, this.currenttagindex);
        }
    }
}
</script>

