<template>
<div>
    <div class="session">
        <div class="title">
            <div></div>
            選擇場次
        </div>
        <div class="availableinfo">
            <span class="left">活動剩餘名額：<span class="num1">{{availablenum}}人</span></span>
            <span>備取剩餘名額：<span class="num1">5人</span></span>
        </div>
        <div class="ticketwrap">
            <Ticket></Ticket>
        </div>
    </div>
    <div class="btnwrap">
        <fillbtn btnname="填寫報名資料" width="329" height="45" @clickhandler="tosignupinfo"></fillbtn>
    </div>
    <alertblock v-show="showalert"></alertblock>
</div>
</template>

<script>
import fillbtn from '../components/fillbtn'
import Ticket from '../components/ticket'
import axios from 'axios'
import alertblock from '../components/Alertblock'
export default {
    data() {
        return {
            eventmemberlist: [],
            availablenum: 0,
            waitingnum:0
        }
    },
    mounted() {
        axios({
                method: 'get',
                url: 'https://script.google.com/macros/s/AKfycbxqMsajmkgXY6oxaJlCh3tRP5Rr8MEh0GpBSkcR7amFy7cL_YorIGZvftoQnSv6Nn7R8Q/exec',
                params: {
                    mode: 'user',
                    id: this.signupinfolist[17]
                }
            })
            .then((response) => {
                console.log(response.data)
                this.availablenum = this.signupinfolist[7] - response.data.length;
                this.eventmemberlist = response.data
                // this.signupmembernum = response.data

            })
            .catch((error) => {
                console.error(error)
            })
    },
    methods: {
        tosignupinfo() {
            if (this.hassignup == true) {
                // alert('一人只能報名一次')
                this.$store.dispatch('showalertani', 'show')
                setTimeout(() => {
                    this.$store.dispatch('showalertani', 'close')
                }, 2000);
            } else {
                this.$store.commit('addsignupnumber')
            }

        }
    },
    computed: {
        signupinfolist() {
            return this.$store.state.signupinfo
        },
        hassignup() {
            let hassignup = false;
            for (let i = 0; i < this.eventmemberlist.length; i++) {
                if (this.eventmemberlist[i][0] == this.userdata.displayName) {
                    hassignup = true
                }
            }
            return hassignup
        },
        showalert() {
            return this.$store.state.alertshow
        },
        userdata() {
            return this.$store.state.userprofiledata;
        }

    },
    components: {
        fillbtn,
        Ticket,
        alertblock
    }
}
</script>

